"use client"
import React, { useState } from "react";
import { Paper, TextField, Button, Grid, Typography } from "@mui/material";
import { Skeleton } from "@/components/ui/skeleton"; // Si estás usando el componente Skeleton
import Image from "next/image"; // Si usas Next.js, puedes usar este componente para optimizar las imágenes
import { Heart } from "lucide-react"; // Ícono de corazón de Lucide
import { ToastContainer, toast } from "react-toastify";
import { uploadImage } from "../../utils/supabaseServices"; // Asegúrate de tener la función para subir imágenes a Supabase

import "react-toastify/dist/ReactToastify.css"; // Necesario para los mensajes toast

const ProductPublish = () => {
  const [product, setProduct] = useState({
    name: '',
    description: '',
    price: '',
    fragancia: '',
    intensidad: 1,
    image: '',
    image1: '',
  });

  // Función para manejar el cambio en los campos del formulario
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setProduct((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  // Función para manejar la carga de imágenes
  const handleImageUpload = async (e) => {
    const file = e.target.files ? e.target.files[0] : null;
    if (file) {
      const uploadedImageUrl = await uploadImage(file); // Usando la función uploadImage para subir la imagen a Supabase
      setProduct((prev) => ({
        ...prev,
        image: uploadedImageUrl,
      }));
    }
  };

  // Función para manejar la carga de la segunda imagen
  const handleImage1Upload = async (e) => {
    const file = e.target.files ? e.target.files[0] : null;
    if (file) {
      const uploadedImageUrl = await uploadImage(file); // Usando la función uploadImage para subir la imagen a Supabase
      setProduct((prev) => ({
        ...prev,
        image1: uploadedImageUrl,
      }));
    }
  };

  // Función para publicar el producto
  const handlePublish = () => {
    // Aquí debes hacer la conexión con Supabase para guardar los datos del producto
    toast.success("Producto publicado con éxito");

    // Limpiar el formulario después de publicar
    setProduct({
      name: '',
      description: '',
      price: '',
      fragancia: '',
      intensidad: 1,
      image: '',
      image1: '',
    });
  };

  return (
    <Grid container spacing={2} className="p-8">
      <ToastContainer position="bottom-right" />
      {/* Formulario de publicación */}
      <Grid item xs={12} md={8}>
        <Paper sx={{ padding: 2 }}>
          <Typography variant="h6">Publicar un Producto</Typography>

          <TextField
            fullWidth
            label="Nombre del Producto"
            name="name"
            value={product.name}
            onChange={handleInputChange}
            sx={{ marginBottom: 2 }}
          />
          <TextField
            fullWidth
            label="Descripción"
            name="description"
            value={product.description}
            onChange={handleInputChange}
            sx={{ marginBottom: 2 }}
          />
          <TextField
            fullWidth
            label="Precio"
            name="price"
            value={product.price}
            onChange={handleInputChange}
            sx={{ marginBottom: 2 }}
          />
          <TextField
            fullWidth
            label="Fragancia"
            name="fragancia"
            value={product.fragancia}
            onChange={handleInputChange}
            sx={{ marginBottom: 2 }}
          />

          <div className="mb-2">
            <label className="block mb-1">Intensidad</label>
            <input
              type="range"
              min="1"
              max="5"
              name="intensidad"
              value={product.intensidad}
              onChange={handleInputChange}
              className="w-full"
            />
            <div className="flex justify-between text-xs">
              <span>1</span>
              <span>5</span>
            </div>
          </div>

          {/* Carga de imágenes */}
          <div className="mb-2">
            <label>Imagen Principal</label>
            <input type="file" onChange={handleImageUpload} className="block" />
          </div>
          <div className="mb-2">
            <label>Imagen Secundaria</label>
            <input type="file" onChange={handleImage1Upload} className="block" />
          </div>

          {/* Botón para publicar */}
          <Button
            variant="contained"
            color="primary"
            sx={{ marginTop: 2 }}
            onClick={handlePublish}
          >
            Publicar Producto
          </Button>
        </Paper>
      </Grid>

      {/* Card de Previsualización */}
      <Grid item xs={12} md={4}>
        <Paper sx={{ padding: 2 }}>
          <Typography variant="h6">Vista Previa</Typography>
          <div className="relative flex flex-col items-center cursor-pointer">
            {/* Ícono de corazón */}
            <div className="absolute top-2 right-2 p-1 rounded-full">
              <Heart className="text-black w-6 h-6 hover:fill-black hover:text-black transition-all" />
            </div>

            {/* Contenedor de imágenes */}
            <div className="relative w-full h-[300px]">
              {/* Imagen principal */}
              <img
                src={product.image || ""}
                alt={product.name}
                className="w-full h-full object-cover rounded-md transition duration-300 ease-in-out"
              />
              {/* Imagen secundaria (para hover) */}
              <img
                src={product.image1 || ""}
                alt={product.name}
                className="w-full h-full object-cover rounded-md absolute top-0 left-0 opacity-0 transition duration-300 ease-in-out hover:opacity-100"
              />
            </div>

            {/* Información del producto */}
            <h3 className="mt-4 text-lg font-semibold text-center">
              {product.name || "Nombre del Producto"}
            </h3>
            <p className="text-sm text-muted-foreground text-center">
              {product.description || "Descripción del producto..."}
            </p>
            <span className="text-md font-bold text-center">
              ${product.price || "Precio"}
            </span>

            {/* Botón de añadir al carrito */}
            <button className="mt-2 px-4 py-2 bg-black text-white rounded-lg">
              Añadir al carrito
            </button>
          </div>
        </Paper>
      </Grid>
    </Grid>
  );
};

export default ProductPublish;
